import {
  AfterContentChecked,
  ChangeDetectorRef,
  Component,
  HostListener,
  OnInit,
} from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { fadeIn, fadeOut, listAnimation } from '../app.animations';
import { PointsService } from '../services/points.service';
import {
  faChevronLeft,
  faChevronRight,
} from '@fortawesome/free-solid-svg-icons';
import {
  trigger,
  transition,
  query,
  style,
  animate,
  group,
} from '@angular/animations';

@Component({
  selector: 'app-quiz-panel',
  templateUrl: './quiz-panel.component.html',
  styleUrls: ['./quiz-panel.component.scss'],
  animations: [
    trigger('animSlider', [
      transition(':increment', [
        query(
          ':enter, :leave',
          style({ position: 'fixed', width: '{{width}}' }),
          {
            optional: true,
          }
        ),
        group([
          query(
            ':enter',
            [
              style({ transform: 'translateX(10%)' }),
              animate('.3s ease-out', style({ transform: 'translateX(0%)' })),
            ],
            {
              optional: true,
            }
          ),
          query(
            ':leave',
            [
              style({ transform: 'translateX(0%)' }),
              animate(
                '.4s ease-out',
                style({ transform: 'translateX(-10%)', opacity: 0 })
              ),
            ],
            {
              optional: true,
            }
          ),
        ]),
      ]),
      transition(':decrement', [
        query(
          ':enter, :leave',
          style({ position: 'fixed', width: '{{width}}' }),
          {
            optional: true,
          }
        ),
        group([
          query(
            ':enter',
            [
              style({ transform: 'translateX(-10%)' }),
              animate('.3s ease-out', style({ transform: 'translateX(0%)' })),
            ],
            {
              optional: true,
            }
          ),
          query(
            ':leave',
            [
              style({ transform: 'translateX(0%)' }),
              animate(
                '.4s ease-out',
                style({ transform: 'translateX(10%)', opacity: 0 })
              ),
            ],
            {
              optional: true,
            }
          ),
        ]),
      ]),
    ]),
    listAnimation,
    fadeIn,
    fadeOut,
  ],
})
export class QuizPanelComponent implements OnInit, AfterContentChecked {
  screenWidth: number;

  @HostListener('window:resize', ['$event'])
  onResize() {
    this.screenWidth = window.innerWidth;
  }

  faChevronLeft = faChevronLeft;
  faChevronRight = faChevronRight;
  counter: number = 0;
  showOptions = false;
  form: FormGroup;
  percentMatching = 70;
  points: number = 0;

  first;
  second;
  third;
  fourth;

  questions: any[] = [
    {
      title: 'Τι είναι το «01» ;',
      formName: 'first',
      options: [
        {
          option:
            'ΟΑΣΘ: Τ.Σ. ΕΥΚΑΡΠΙΑΣ - ΣΚΛΑΒΕΝΙΤΗΣ - ΝΟΣ. ΠΑΠΑΓΕΩΡΓΙΟΥ - Κ.Τ.Ε.Λ. ',
          tip: 'Είσαι «αστικός» μύθος',
        },
        {
          option: 'Dukes of Hazard, General Lee',
          tip: 'Πόσο χρονών είσαι είπαμε;',
        },
        { option: 'Δυαδικός Κώδικάς', tip: 'Χμμμ… έχεις potential…' },
      ],
      answer: 2,
    },
    {
      title: 'What is QA;',
      formName: 'second',
      options: [
        { option: 'Queen Alexandra', tip: 'Ok, είσαι για τον εξώστη' },
        { option: 'Question and Answer', tip: 'Μήπως το πάς για influencer?' },
        { option: 'Quality assurance', tip: 'Η φύλαρχος σε χαιρετά!' },
      ],
      answer: 2,
    },
    {
      title: ' Delivery, τι σου θυμίζει;',
      formName: 'third',
      options: [
        { option: 'Σουβλάκια', tip: 'Χρειάζεσαι γαστρονομική φροντίδα' },
        {
          option: 'Φακέλους και δέματα',
          tip: 'Η καραντίνα τελείωσε, μπορείς να βγεις…',
        },
        {
          option: '«Continuous» και ο νοών νοήτω',
          tip: 'H φυλή υποκλίνεται στο μεγαλείο σου!',
        },
      ],
      answer: 2,
    },
    {
      title: 'What this means to you',
      formName: 'fourth',
      image: 'assets/img/java_mug_coffee.png',
      options: [
        {
          option: 'Θα μου πούν το φλυτζάνι',
          tip: 'Χμμμ…',
        },
        {
          option: 'Καφεεεε…απλά',
          tip: 'Hmmm…σε έχουμε καλύψει με μια - ',
          image: 'assets/img/coffee-maker-white.png',
        },
        {
          option: 'Java challenge',
          tip: 'Κυλά στις φλέβες σου αίμα της φυλής…',
        },
      ],
      answer: 2,
    },
  ];

  constructor(
    fb: FormBuilder,
    private pointsService: PointsService,
    private ref: ChangeDetectorRef
  ) {
    this.form = fb.group({
      first: [''],
      second: [''],
      third: [''],
      fourth: [''],
    });

    this.onResize();
  }

  ngOnInit(): void {
    setTimeout(() => {
      this.showOptions = true;
    }, 500);
  }

  ngAfterContentChecked(): void {
    this.ref.detectChanges();
  }

  radioChecked(item, formName) {
    this.form.get(formName).setValue(item.option);
    const answer = this.questions[this.counter].answer;

    const points =
      this.questions[this.counter].options[answer].option ===
      this.form.get(formName).value
        ? 10
        : 5;

    this.points = this.points + points;

    this.pointsService.changePoints({ active: true, points: this.points });

    setTimeout(() => {
      this.pointsService.changePoints({ active: false, points: this.points });
    }, 3000);
  }

  onNext() {
    if (this.counter != this.questions.length) {
      this.counter++;
      this.showOptions = false;

      setTimeout(() => {
        this.showOptions = true;
      }, 500);
    }

    if (this.counter === this.questions.length) {
      this.calculateMatching();
    }
  }

  questionAnswered(formName) {
    return this.form.get(formName).value === '';
  }

  isMobile() {
    return this.screenWidth <= 800;
  }

  calculateMatching() {
    let correctAnswers = 0;
    const questionsControls = ['first', 'second', 'third', 'fourth'];

    questionsControls.forEach((control, index) => {
      const answer = this.questions[index].answer;
      this.questions[index].options[answer].option ===
        this.form.get(control).value && correctAnswers++;
    });

    switch (correctAnswers) {
      case 0: {
        this.percentMatching = 70;
        break;
      }
      case 1: {
        this.percentMatching = 80;
        break;
      }
      case 2: {
        this.percentMatching = 90;
        break;
      }
      case 3: {
        this.percentMatching = 95;
        break;
      }
      case 4: {
        this.percentMatching = 100;
        break;
      }
    }
  }

  isCorrectAnswer(item, question) {
    return (
      this.questions[this.counter].options[question.answer].option ===
      item.option
    );
  }

  isWrongAnswer(item, question) {
    return (
      this.form.get(question.formName).value &&
      this.questions[this.counter].options[question.answer].option !==
        item.option
    );
  }

  checkIfChecked(item, formName) {
    const val = this.form.get(formName).value;
    return val === item.option;
  }

  onPrevious() {
    if (this.counter > 0) {
      this.counter--;

      this.showOptions = false;

      setTimeout(() => {
        this.showOptions = true;
      }, 500);
    }
  }
}
